<template>
  <div class="the-header">
    <!--icon-->
    <div class="header-logo" @click="goHome">
      <svg class="icon" aria-hidden="true">
        <use :xlink:href="ERJI"></use>
      </svg>
      <span>{{ musicName }}</span>
    </div>
    <ul class="navbar" ref="change">
      <li
        :class="{ active: item.name === activeName }"
        v-for="item in navMsg"
        :key="item.path"
        @click="goPage(item.path, item.name)"
      >
        {{ item.name }}
      </li>
      <li>
        <div class="header-search">
          <input
            type="text"
            placeholder="Search song"
            @keyup.enter="goSearch()"
            v-model="keywords"
          />
          <div class="search-btn" @click="goSearch()">
            <svg class="icon" aria-hidden="true">
              <use :xlink:href="SOUSUO"></use>
            </svg>
          </div>
        </div>
      </li>
      <li
        v-show="!loginIn"
        :class="{ active: item.name === activeName }"
        v-for="item in loginMsg"
        :key="item.type"
        @click="goPage(item.path, item.name)"
      >
        {{ item.name }}
      </li>
    </ul>
    <div class="header-right" v-show="loginIn">
      <div id="user">
        <img :src="attachImageUrl(avator)" alt="" />
      </div>
      <ul class="menu">
        <li
          v-for="(item, index) in menuList"
          :key="index"
          @click="goMenuList(item.path)"
        >
          {{ item.name }}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import mixin from "../mixins";
import { mapGetters } from "vuex";
import { navMsg, loginMsg, menuList } from "../assets/data/header";
import { ICON } from "../assets/icon/index";

export default {
  name: "the-header",
  mixins: [mixin],
  data() {
    return {
      musicName: "Jake-music",
      navMsg: navMsg,
      loginMsg: loginMsg,
      menuList: menuList,
      keywords: "",
      ERJI: ICON.ERJI,
      SOUSUO: ICON.SOUSUO
    };
  },
  computed: {
    ...mapGetters(["userId", "activeName", "avator", "username", "loginIn"])
  },
  mounted() {
    document.querySelector("#user").addEventListener(
      "click",
      function(e) {
        document.querySelector(".menu").classList.add("show");
        e.stopPropagation();
      },
      false
    );

    document.querySelector(".menu").addEventListener(
      "click",
      function(e) {
        e.stopPropagation();
      },
      false
    );
    document.addEventListener(
      "click",
      function() {
        document.querySelector(".menu").classList.remove("show");
      },
      false
    );
  },
  methods: {
    goHome() {
      this.$router.push({ path: "/" });
    },
    goPage(path, value) {
      document.querySelector(".menu").classList.remove("show");
      this.changeIndex(value);
      if (!this.loginIn && path === "/my-music") {
        this.notify("Please login first", "warning");
      } else {
        this.$router.push({ path: path });
      }
    },
    changeIndex(value) {
      this.$store.commit("setActiveName", value);
    },
    goMenuList(path) {
      if (path === 0) {
        this.$store.commit("setIsActive", false);
      }
      document.querySelector(".menu").classList.remove("show");
      if (path) {
        this.$router.push({ path: path });
      } else {
        this.$store.commit("setLoginIn", false);
        this.$router.go(0);
      }
    },
    goSearch() {
      this.$store.commit("setSearchword", this.keywords);
      this.$router.push({
        path: "/search",
        query: { keywords: this.keywords }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../assets/css/the-header.scss";
</style>



