<template>
  <div class="play-bar" :class="{ show: !toggle }">
    <div @click="toggle = !toggle" class="item-up" :class="{ turn: toggle }">
      <svg class="icon" aria-hidden="true">
        <use :xlink:href="ZHEDIE"></use>
      </svg>
    </div>
    <div class="kongjian">
      <!--last song-->
      <div class="item" @click="prev">
        <svg class="icon" aria-hidden="true">
          <use :xlink:href="SHANGYISHOU"></use>
        </svg>
      </div>
      <!--play-->
      <div class="item" @click="togglePlay">
        <svg class="icon" aria-hidden="true">
          <use :xlink:href="playButtonUrl"></use>
        </svg>
      </div>
      <!--next song-->
      <div class="item" @click="next">
        <svg class="icon" aria-hidden="true">
          <use :xlink:href="XIAYISHOU"></use>
        </svg>
      </div>
      <!--picture-->
      <div class="item-img" @click="goPlayerPage">
        <img :src="picUrl" alt="" />
      </div>
      <!--progress-->
      <div class="playing-speed">
        <!--start time-->
        <div class="current-time">{{ nowTime }}</div>
        <div class="progress-box">
          <div class="item-song-title">
            <div>{{ this.title }}</div>
            <div>{{ this.artist }}</div>
          </div>
          <div ref="progress" class="progress" @mousemove="mousemove">
            <!--progress bar-->
            <div ref="bg" class="bg" @click="updatemove">
              <div
                ref="curProgress"
                class="cur-progress"
                :style="{ width: curLength + '%' }"
              ></div>
            </div>
            <!--progress end -->
            <!--moving pointer-->
            <div
              ref="idot"
              class="idot"
              :style="{ left: curLength + '%' }"
              @mousedown="mousedown"
              @mouseup="mouseup"
            ></div>
            <!-- end -->
          </div>
        </div>
        <!--end of playing-->
        <div class="left-time">{{ songTime }}</div>
      </div>
      <!--volumn-->
      <div class="item icon-volume">
        <svg v-if="volume !== 0" class="icon" aria-hidden="true">
          <use :xlink:href="YINLIANG"></use>
        </svg>
        <svg v-else class="icon" aria-hidden="true">
          <use :xlink:href="JINGYIN"></use>
        </svg>
        <el-slider class="volume" v-model="volume" :vertical="true"></el-slider>
      </div>
      <!--add-->
      <div class="item" @click="collection">
        <svg :class="{ active: isActive }" class="icon" aria-hidden="true">
          <use :xlink:href="XIHUAN"></use>
        </svg>
      </div>
      <!--download-->
      <div class="item" @click="downloadMusic">
        <svg class="icon" aria-hidden="true">
          <use :xlink:href="XIAZAI"></use>
        </svg>
      </div>
      <!--list-->
      <div class="item" @click="changeAside">
        <svg class="icon" aria-hidden="true">
          <use :xlink:href="LIEBIAO"></use>
        </svg>
      </div>
    </div>
  </div>
</template>

<script>
import mixin from "../mixins";
import { mapGetters } from "vuex";
import { HttpManager } from "../api/index";
import { ICON } from "../assets/icon/index";

export default {
  name: "play-bar",
  mixins: [mixin],
  data() {
    return {
      tag: false,
      nowTime: "00:00",
      songTime: "00:00",
      curLength: 0,
      progressLength: 0,
      mouseStartX: 0,
      toggle: true,
      volume: 50,
      XIAZAI: ICON.XIAZAI,
      ZHEDIE: ICON.ZHEDIE,
      SHANGYISHOU: ICON.SHANGYISHOU,
      XIAYISHOU: ICON.XIAYISHOU,
      YINLIANG: ICON.YINLIANG1,
      JINGYIN: ICON.JINGYIN,
      LIEBIAO: ICON.LIEBIAO,
      XIHUAN: ICON.XIHUAN
    };
  },
  computed: {
    ...mapGetters([
      "loginIn",
      "userId",
      "isPlay",
      "playButtonUrl",
      "id",
      "url",
      "title",
      "artist",
      "picUrl",
      "curTime",
      "duration",
      "listOfSongs",
      "listIndex",
      "showAside",
      "autoNext",
      "isActive"
    ])
  },
  watch: {
    isPlay(val) {
      if (val) {
        this.$store.commit("setPlayButtonUrl", ICON.ZANTING);
      } else {
        this.$store.commit("setPlayButtonUrl", ICON.BOFANG);
      }
    },
    volume() {
      this.$store.commit("setVolume", this.volume / 100);
    },
    curTime() {
      this.nowTime = this.formatSeconds(this.curTime);
      this.songTime = this.formatSeconds(this.duration);
      this.curLength = (this.curTime / this.duration) * 100;
    },
    autoNext() {
      this.next();
    }
  },
  mounted() {
    this.progressLength = this.$refs.progress.getBoundingClientRect().width;
    document.querySelector(".icon-volume").addEventListener(
      "click",
      function(e) {
        document.querySelector(".volume").classList.add("show-volume");
        e.stopPropagation();
      },
      false
    );
    document.querySelector(".volume").addEventListener(
      "click",
      function(e) {
        e.stopPropagation();
      },
      false
    );
    document.addEventListener(
      "click",
      function() {
        document.querySelector(".volume").classList.remove("show-volume");
      },
      false
    );
  },
  methods: {
    downloadMusic() {
      HttpManager.downloadMusic(this.url)
        .then(res => {
          let content = res.data;
          let eleLink = document.createElement("a");
          eleLink.download = `${this.artist}-${this.title}.mp3`;
          eleLink.style.display = "none";
          let blob = new Blob([content]);
          eleLink.href = URL.createObjectURL(blob);
          document.body.appendChild(eleLink);
          eleLink.click();
          document.body.removeChild(eleLink);
        })
        .catch(err => {
          console.log(err);
        });
    },
    changeAside() {
      let temp = !this.showAside;
      this.$store.commit("setShowAside", temp);
    },
    togglePlay() {
      if (this.isPlay) {
        this.$store.commit("setIsPlay", false);
      } else {
        this.$store.commit("setIsPlay", true);
      }
    },
    formatSeconds(value) {
      let theTime = parseInt(value);
      let theTime1 = 0;
      let theTime2 = 0;
      if (theTime > 60) {
        theTime1 = parseInt(theTime / 60);
        theTime = parseInt(theTime % 60);
        if (theTime1 > 60) {
          theTime2 = parseInt(theTime1 / 60);
          theTime1 = 60;
        }
      }
      if (parseInt(theTime) < 10) {
        var result = "0:0" + parseInt(theTime);
      } else {
        result = "0:" + parseInt(theTime);
      }
      if (theTime1 > 0) {
        if (parseInt(theTime) < 10) {
          result = "0" + parseInt(theTime);
        } else {
          result = parseInt(theTime);
        }
        result = parseInt(theTime1) + ":" + result;
      }
      if (theTime2 > 0) {
        if (parseInt(theTime) < 10) {
          result = "0" + parseInt(theTime);
        } else {
          result = parseInt(theTime);
        }
        result = parseInt(theTime2) + ":" + parseInt(theTime1) + ":" + result;
      }
      return result;
    },
    mousedown(e) {
      this.mouseStartX = e.clientX;
      this.tag = true;
    },
    // 拖拽结束
    mouseup() {
      this.tag = false;
    },
    // 拖拽中
    mousemove(e) {
      if (!this.duration) {
        return false;
      }
      if (this.tag) {
        let movementX = e.clientX - this.mouseStartX;
        let curLength = this.$refs.curProgress.getBoundingClientRect().width;
        this.progressLength = this.$refs.progress.getBoundingClientRect().width;
        let newPercent = ((curLength + movementX) / this.progressLength) * 100;
        if (newPercent > 100) {
          newPercent = 100;
        }
        this.curLength = newPercent;
        this.mouseStartX = e.clientX;
        this.changeTime(newPercent);
      }
    },
    changeTime(percent) {
      let newCurTime = this.duration * (percent * 0.01);
      this.$store.commit("setChangeTime", newCurTime);
    },
    updatemove(e) {
      if (!this.tag) {
        let curLength = this.$refs.bg.offsetLeft;
        this.progressLength = this.$refs.progress.getBoundingClientRect().width;
        let newPercent = ((e.clientX - curLength) / this.progressLength) * 100;
        if (newPercent < 0) {
          newPercent = 0;
        } else if (newPercent > 100) {
          newPercent = 100;
        }
        this.curLength = newPercent;
        this.changeTime(newPercent);
      }
    },
    prev() {
      if (this.listIndex !== -1 && this.listOfSongs.length > 1) {
        if (this.listIndex > 0) {
          this.$store.commit("setListIndex", this.listIndex - 1);
          this.toPlay(this.listOfSongs[this.listIndex].url);
        } else {
          this.$store.commit("setListIndex", this.listOfSongs.length - 1);
          this.toPlay(this.listOfSongs[this.listIndex].url);
        }
      }
    },
    next() {
      if (this.listIndex !== -1 && this.listOfSongs.length > 1) {
        if (this.listIndex < this.listOfSongs.length - 1) {
          this.$store.commit("setListIndex", this.listIndex + 1);
          this.toPlay(this.listOfSongs[this.listIndex].url);
        } else {
          this.$store.commit("setListIndex", 0);
          this.toPlay(this.listOfSongs[0].url);
        }
      }
    },
    toPlay(url) {
      if (url && url !== this.url) {
        this.$store.commit("setId", this.listOfSongs[this.listIndex].id);
        this.$store.commit("setUrl", this.$store.state.configure.HOST + url);
        this.$store.commit(
          "setpicUrl",
          this.$store.state.configure.HOST +
            this.listOfSongs[this.listIndex].pic
        );
        this.$store.commit(
          "setTitle",
          this.replaceFName(this.listOfSongs[this.listIndex].name)
        );
        this.$store.commit(
          "setArtist",
          this.replaceLName(this.listOfSongs[this.listIndex].name)
        );
        this.$store.commit(
          "setLyric",
          this.parseLyric(this.listOfSongs[this.listIndex].lyric)
        );
      }
    },
    goPlayerPage() {
      this.$router.push({ path: `/lyric/${this.id}` });
    },
    collection() {
      if (this.loginIn) {
        var params = new URLSearchParams();
        params.append("userId", this.userId);
        params.append("type", 0);
        params.append("songId", this.id);
        HttpManager.setCollection(params)
          .then(res => {
            if (res.code === 1) {
              this.$store.commit("setIsActive", true);
              this.notify("Successful collection", "success");
            } else if (res.code === 2) {
              this.notify("Already collected", "warning");
            } else {
              this.$notify.error({
                title: "Fail to collect this song",
                showClose: false
              });
            }
          })
          .catch(err => {
            console.log(err);
          });
      } else {
        this.notify("Please login first", "warning");
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../assets/css/play-bar.scss";
</style>



